import { useSpring, animated, config } from 'react-spring'






export default function Loading_screen({visible_flag}){
  const props = useSpring({

    loop: true,
    to: { opacity: 1, top:'50vh',   },
    from: { opacity: 0, top:'0vh',  },
    config: config.molasses,
  })

return(
  <div >
    <animated.h1 style={{position:'absolute', visible:{visible_flag}, left:'50vw',  color:'white', ...props}}>
      LOADING...
    </animated.h1>
  </div >
)
}
